.skills {
    background-color: #f8f8f8;
    padding: 20px;
    margin-top: 10px;

    h2 {
        color: #333;
    }

    ul {
        list-style: none;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    li {
        margin: 5px 10px;
        padding: 5px;
        background-color: #e0e0e0;
        border-radius: 5px;
    }
}
